<template>
    <div>
        <canvas id="hex-canvas" width="2200" height="2200" @click='click'></canvas>
    </div>
    
</template>

<script>
import global_ from "./common";
import HexagonGrid from "../utils/hexagon"
import {test} from "../utils/testCode"
import bus from '../utils/eventBus'
import agentBus from '../utils/eventBus'

export default {
    name: "Map" ,
    data() {
        return {
            x: String,
            y: String
        }
    },
    mounted() {
        let hexagonGrid = new HexagonGrid('hex-canvas', 25, (tile) => {
            this.x = tile.row;
            this.y = tile.column;
            bus.$emit('showClick', tile)
        });
        hexagonGrid.drawHexGrid(50, 50, 0, 0, true);



        //对agent(./humanSelector.vue)
        bus.$on('drawAgent', (res) => {
            let url = '';
            JSON.pares('{"soldier":"./img/soldier.svg","chariots":"./img/chariots.svg", "tank":"./img/tank.svg","commander":"./img/commander.svg"}', function(k, v){
                if(k == res.type) url = v;
            });
            hexagonGrid.addIcon(res.row,res.column,url);
        });

        //目前是本地的，理论上可以融合到上面那一个单个的，等跑通了再调整
        bus.$on('drawArea', (res) =>{
            let url = '';
            JSON.parse('{"mountain":"./img/mountain.svg","plain":"./img/plain.svg", "river":"./img/river.svg","city":"./img/city.svg","forests":"./img/forests.svg"}', function(k,v){
                if(k == res.type) url = v;
            });
            hexagonGrid.addIcon(res.row, res.column, url);
        });
    },
    methods: {
        click(){
            
        }
    }
}
</script>

