<template>
<div>
    <el-select v-model="label" placeholder="请选择" @change="select">
        <el-option
        v-for="item in options"
        :key="item.label"
        :label="item.label"
        :value="item.value">
        </el-option>
    </el-select>
     <el-button type="text" @click="open">确认</el-button>
    <!-- <confirmButton/> -->
</div>
</template>

<script>
    // import confirmButton from './confirm'
    import bus from '../utils/eventBus'

  export default {
    name:"humanSelector",
    components:{
        // confirmButton
    },
    data() {
      return {
         tile:{},
        options: [{
          value: '1',
          label: '单兵'
        }, {
          value: '2',
          label: '战车'
        }, {
          value: '3',
          label: '坦克'
        }, {
          value: '4',
          label: '指挥官'
        }],
        label: ''
      }
    },
    mounted() {
        bus.$on('showClick', (tile) => {
            this.tile=tile;
        })
    },
    methods: {
      select(label) {
          this.label=label;
      },
      open() {
        if(Object.keys(this.tile).length == 0 || this.label == ''){
          this.$alert("请选择对应的区域或者agent");
        }else{
          this.$alert('确定在' + this.tile.row
          + '行' + this.tile.column + '列添加agent吗？', '添加吗', {
            confirmButtonText: '确定',
            callback: action => {
              //this.addAgentInBackend();
              this.addAgentInMap();
              this.$message({
                type: 'info',
                message: `action: ${ action }`
              });
            }
          });
        }
        
      },
      /*
      //后面做的功能，先把agent选择的功能提交给后台，然后后台判断能不能添加，我再去获取信息
      addAgentInBackend(){
          let url='/agent/x=3&y=4'
          let data={"type":1}
          this.$post(url,data, "post").then(res => {
           console.log(res);
        }).catch(err => {
          console.log(err);
        })
      },
      */
      addAgentInMap(){
          let url='/addAgent/x=' + this.tile.row
            + '&y=' + this.tile.column 
            + 'type=' + this.label

          //告诉服务器，然后进行画图操作
          this.$post(url,"", "post").then(res => {
              bus.$emit("drawAgent", res.data.data)
            }).catch(err => {
              console.log(err);
            })
      }
    }
    
  }
</script>
